
import { Options, Vue } from "vue-class-component";
import { Picker, Popup, Button } from "../packages";
import moment from "moment";

// PickerColumns
@Options({
  components: {
    FanPicker: Picker,
    Popup,
    FanButton: Button,
  },
})
export default class ScorePage extends Vue {
  travelDate = "";
  moment = moment;
  dateTimeObj = this.setDate();
  dateTimeArray = [
    this.dateTimeObj.dateArr,
    this.dateTimeObj.timeHHArr,
    this.dateTimeObj.timeMMArr,
  ]; //二维数组，长度是多少是几列
  dateTimeIndex = [0, 4, 7];

  created() {
    // console.log(this.dateTimeObj);

    setTimeout(() => {
      console.log(this.dateTimeArray);
    }, 1000);
  }

  dateTimeChange(e: any) {
    console.log(e);
    
    // const d = this.dateTimeObj.dateArr[e.detail.value[0]].value;
    // const h = this.dateTimeObj.timeHHArr[e.detail.value[1]].value;
    // const m = this.dateTimeObj.timeMMArr[e.detail.value[2]].value;
    // this.travelDate = `${d} ${h}:${m}`;
  }

  setDate() {
    const dateArr = [];
    for (let i = 0; i < 50; i++) {
      // 默认50天
      const itemDate = moment().add(i, "day");
      const d = itemDate.day();
      let week = "";
      if (d === 0) {
        week = "日";
      }
      if (d === 1) {
        week = "一";
      }
      if (d === 2) {
        week = "二";
      }
      if (d === 3) {
        week = "三";
      }
      if (d === 4) {
        week = "四";
      }
      if (d === 5) {
        week = "五";
      }
      if (d === 6) {
        week = "六";
      }
      dateArr.push({
        name: `${itemDate.format("MM月DD日")}  周${week}`,
        value: itemDate.format("YYYY-MM-DD"),
      });
    }
    const timeHHArr = [];
    for (let i = 0; i < 24; i++) {
      let str = i < 10 ? `0${i}` : `${i}`;
      timeHHArr.push({ name: str, value: str });
    }

    const timeMMArr = [];
    for (let i = 0; i < 60; i++) {
      let str = i < 10 ? `0${i}` : `${i}`;
      timeMMArr.push({ name: str, value: str });
    }

    return {
      dateArr,
      timeHHArr,
      timeMMArr,
    };
  }
}
