import {
  defineComponent,
  PropType, watch
} from "vue";

import Columns, { ColType } from "./columns"
import Popup from '../popup'

export type PickerColumnsViod = {
  value: string;
  defaultIndex: number | string;
}



export type PickerType = "selector" | "multiSelector" | "linkage"
export type PickerMode = "selector" | "multiSelector"

type valueType = unknown;// Number | Array<number>; //un

export const PcikerProps = {
  columns: { type: Array as PropType<string[] | ColType[]>, default: [] },
  className: { type: String, default: "" },
  title: { type: String, default: "请选择" },
  type: { type: String as PropType<PickerType>, default: "base" },
  visibleItemCount: { type: [Number, String], default: 6 },
  defaultIndex: { type: [Number, String], default: 2 },


  mode: { type: String as PropType<PickerMode>, default: 'selector' },
  value: { type: Promise as PropType<unknown>, default: 2 },
  range: { type: Array as PropType<ColType[]>, default: () => { return [] } },
  rangeKey: { type: String, default: "name" },
}

export default defineComponent({
  name: "fan-picker",
  props: PcikerProps,
  emits: ['confirm', 'cancel', 'change'],
  setup(props, { emit, slots }) {
    let retBaseData: any = typeof props.value !== 'object' ? props.value : props.value;
    const retMultiData: any = typeof props.value !== 'object' ? props.value : props.value;

    const onEnter = () => {
      if (props.mode === 'selector') {
        emit('confirm', retBaseData)
      }
      if (props.mode === 'multiSelector') {
        emit('confirm', retMultiData)
      }
    }
    const onCancel = () => {
      if (props.mode === 'selector') {
        emit('cancel', retBaseData)
      }
      if (props.mode === 'multiSelector') {
        emit('cancel', retMultiData)
      }
    }

    // const setData = () => {
    //   if (props.mode === 'selector') {
    //     // retBaseData.defaultIndex = props.defaultIndex;
    //     // retBaseData.value = props.range[Number(props.defaultIndex)] as string;
    //   }
    //   if (props.mode === 'multiSelector') {

    //     // (props.range).forEach((item: ColType) => {
    //     //   retMultiData.push(
    //     //     // {
    //     //     //   defaultIndex: item.defaultIndex,
    //     //     //   value: item.values[Number(item.defaultIndex)] as string
    //     //     // }
    //     //   )
    //     // })
    //   }
    // }

    // setData()
    // watch(() => props.range, (newVal, oldVal) => {
    //   setData()
    // })

    const onChange = (e: number | string, index: number) => {
      if (props.mode === 'selector') {
        retBaseData = e;
        emit('change', e)
      } else if (props.mode === 'multiSelector') {
        retMultiData[index] = e;
        emit('change', retMultiData)
      }
    }

    return () => {
      console.log(props.value);

      return (
        <div class={`fan-pciker-container ${props.className}`}>
          <div class="fan-picker-title">
            <span class="fan-picker-title-cancel" onClick={onCancel}>取消</span>
            <span class="fan-picker-title-con">{props.title}</span>
            <span class="fan-picker-title-enter" onClick={onEnter}>确定</span>
          </div>
          <div class="fan-pciker-columns">

            {
              props.mode === 'selector' ?
                <Columns
                  columns={props.range}
                  defaultIndex={props.value}
                  rangeKey={props.rangeKey}
                  onChange={(e: number | string) => { onChange(e, 0) }} /> :

                null
            }
            {props.mode === 'multiSelector' ?
              (props.range as ColType[]).map((item: any, index: number) => (
                <Columns
                  columns={item}
                  defaultIndex={(props.value as any)[index]}
                  rangeKey={props.rangeKey}
                  key={index}
                  onChange={(e: number | string) => { onChange(e, index) }}
                />
              )) : null}
          </div>
        </div>
      );
    }
  }
})
